[data-tooltip] {
  position: relative;
  height: 100%;
  cursor: pointer;
  display: inline-block; }
  [data-tooltip]:before, [data-tooltip]:after {
    transition: all .2s ease-out;
    position: absolute;
    visibility: hidden;
    opacity: 0;
    pointer-events: none; }
  [data-tooltip]:before {
    width: 0;
    height: 0;
    border-left: 7px solid transparent;
    border-right: 7px solid transparent;
    border-top: 7px solid #364A4D;
    content: '';
    top: 0;
    left: 50%;
    margin-left: -3.25px;
    z-index: 10001;
    bottom: -10px; }
  [data-tooltip]:after {
    box-sizing: border-box;
    content: attr(data-tooltip);
    padding: 0.5em 0.75em;
    border-radius: 3px;
    font-size: 0.95em;
    z-index: 10000;
    left: 50%;
    bottom: 100%;
    margin-left: -7.5em;
    margin-top: 10px;
    width: 15em;
    line-height: 120%;
    background: #364A4D;
    color: #EEEEEE; }
  [data-tooltip]:hover:before, [data-tooltip]:hover:after {
    visibility: visible;
    opacity: 1; }

